/* iframe layout */
body {
	position: relative;
	background-color: #fafafa;
}
.container {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	-moz-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	-o-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	cursor: pointer;
}
.actions {
	position: absolute;
	right: 6vw;
	bottom: 60px;
	text-align: center;
}
.btn {
	border: none;
	padding: 0.6em 1.2em;
	background: #3f51b5;
	color: #fff;
	font-family: 'Lato', 'Arial', sans-serif;
	font-size: 1.5em;
	letter-spacing: 1px;
	text-transform: uppercase;
	cursor: pointer;
	display: inline-block;
	margin: 3px 2px;
	border-radius: 2px;
	&:focus {
		outline: none;
	}
	&:hover {
		opacity: 0.5;
	}
}
.no-js {
	.actions {
		display: none;
	}
}

/* rotate panel */
.rotate-panel {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-box-flex: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
	-webkit-box-align: end;
	-ms-flex-align: end;
	align-items: flex-end;
	-webkit-transform-origin: 0 0;
	transform-origin: 0 0;
	-webkit-transform: scale(.9) rotateX(56deg) rotateY(0deg) rotate(45deg);
	transform: scale(.9) rotateX(56deg) rotateY(0deg) rotate(45deg);
	width: 100%;
}
.panel {
	-ms-flex-preferred-size: 30%;
	flex-basis: 30%;
	margin-right: 2.18978%;
	margin-top: 2.18978%;
	-webkit-transform-style: preserve-3d;
	transform-style: preserve-3d;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	cursor: pointer;
	outline: none;
	&:last-child,
	&:nth-child(3n) {
		margin-right: 0;
	}
	&:nth-child(3n+2) {
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);
	}
	&:nth-child(3n+3) {
		-webkit-transform: translateY(-100%);
		transform: translateY(-100%);
	}
	&:focus &__item,
	&:hover &__item {
		-webkit-box-shadow: 2vw 1.5vw 25px rgba(0,0,0,.2);
		box-shadow: 2vw 1.5vw 25px rgba(0,0,0,.2);
		-webkit-transform: translate3d(-6%,-6%,-20vh);
		transform: translate3d(-6%,-6%,-20vh);
		-webkit-transition: all .2s cubic-bezier(.11,.83,.29,.83);
		transition: all .2s cubic-bezier(.11,.83,.29,.83)
	}
	&__item {
		min-height: 10vw;
		-webkit-transform: translateZ(0);
		transform: translateZ(0);
		-webkit-transition: all .2s cubic-bezier(.11,.83,.29,.83);
		transition: all .2s cubic-bezier(.11,.83,.29,.83);
		position: relative;
		display: block;
		-webkit-box-shadow: 0.65vw 0.5vw 25px rgba(0,0,0,.2);
		box-shadow: 0.65vw 0.5vw 25px rgba(0,0,0,.2);
		line-height: 0;
	}
	&__link {
		z-index: 1;
		bottom: 0;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
	}
	&__image {
		position: relative;
		overflow: hidden;
		img {
			display: block;
			width: 100%;
			height: 100%;
		}
	}
}

/* baraja demo */
.baraja-demo {
	.actions {
		position: static;
	}
	.btn {
		line-height: 24px;
	}
	.baraja-container {
		margin: 0 auto 150px;
		box-shadow: 4px 4px 10px rgba(0,0,0,0.07);
		border-radius: 10px;
		li {
			padding: 10px;
			border: 1px #eee solid;
			border-radius: 10px;
			box-sizing: border-box;
			img {
				width: 100%;
			}
			h4 {
				margin: 20px 0 10px;
			}
			p {
				padding-top: 10px;
				border-top: 1px #eee solid;
			}
		}
	}
}

/* hover visible */
.hover-visible {
	width: 100%;
	max-width: 1440px;
	margin: 0 auto;
	padding: 4rem 2rem;
	.items {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		.item {
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			overflow: hidden;
			width: 300px;
			height: 200px;
			margin: 5px;
			cursor: pointer;
			background-color: lighten(#2f3238, 5);
			border-radius: 2px;
			box-shadow: 0 5px 5px rgba(0, 0, 0, 0.02), inset 0 0px 0px 1px rgba(0, 0, 0, 0.07);
			transform: translateZ(0);
			&:hover {
				circle,
				image {
					transform: scale(1)
				}
			}
			svg {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				.svg-masked-text {
					fill: rgba(#fff, 1);
				}
				circle {
					transform-origin: 50% 50%;
					transform: scale(0);
					transition: transform 200ms cubic-bezier(0.250, 0.460, 0.450, 0.940);
				}
				text {
					font-size: 1.1rem;
					text-transform: uppercase;
					text-anchor: middle;
					letter-spacing: 1px;
					font-weight: 600;
					fill: lighten(#2f3238, 16);
				}
				image {
					transform: scale(1.1);
					transform-origin: 50% 50%;
					transition: transform 200ms cubic-bezier(0.250, 0.460, 0.450, 0.940);
				}
			}
		}
	}
}

